header.page-header {
  position: relative;
}
header.page-header .text-container {
  position: absolute;
  z-index: 2;
  top: 0;
  width: 100%;
  height: calc(100% - 6px);
  display: flex;
  justify-content: center;
  align-items: center;
}
header.page-header .text-container .text-wrapper {
  max-width: 1340px;
  padding: 0 20px;
  width: 100%;
}
header.page-header .text-container .text-wrapper .text-content {
  max-width: 500px;
}
header.page-header .text-container .text-wrapper .text-content h1.archive-offer-title {
  color: #03dddd;
  font-weight: 900;
  font-size: 42px;
}
@media (max-width: 767px) {
  header.page-header .text-container .text-wrapper .text-content h1.archive-offer-title {
    font-size: 32px;
  }
}
header.page-header .text-container .text-wrapper .text-content .archive-offer-description {
  color: white;
  font-size: 20px;
}

section.offer-options {
  padding: 80px 15px 50px;
  text-align: center;
  display: flex;
  width: 100%;
  justify-content: center;
}
@media (max-width: 767px) {
  section.offer-options {
    padding: 50px 15px 30px;
  }
}
section.offer-options .offer-options-content {
  max-width: 1300px;
  width: 100%;
}
section.offer-options .offer-options-content h2.offer-title {
  font-size: 38px;
  font-weight: 700;
}
@media (max-width: 767px) {
  section.offer-options .offer-options-content h2.offer-title {
    font-size: 27px;
  }
}
section.offer-options h3.offer-count {
  font-size: 26px;
  margin: 15px 0 34px;
}
@media (max-width: 767px) {
  section.offer-options h3.offer-count {
    font-size: 20px;
  }
}
section.offer-options .form-wrapper {
  display: flex;
  gap: 13px;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  section.offer-options .form-wrapper {
    flex-wrap: wrap;
  }
}
section.offer-options .form-wrapper button.get-offers {
  height: 53px;
  color: #00205b;
  background-color: #03dddd;
  padding: 10px 40px 10px 40px;
  min-width: 16%;
}
section.offer-options .form-wrapper button.get-offers:hover {
  background-color: #00205b;
  border-color: #00205b;
  color: #03dddd;
}
section.offer-options .form-wrapper .query-offer {
  width: 20%;
  height: 53px;
  font-size: 25px;
  border-radius: 10px;
}
@media (max-width: 767px) {
  section.offer-options .form-wrapper .query-offer {
    font-size: 22px;
    width: 100%;
    min-width: 100%;
  }
}
@media (min-width: 768px) {
  section.offer-options .form-wrapper .query-offer {
    font-size: 16px;
  }
}
@media (min-width: 992px) {
  section.offer-options .form-wrapper .query-offer {
    font-size: 18px;
  }
}
@media (min-width: 1300px) {
  section.offer-options .form-wrapper .query-offer {
    font-size: 25px;
  }
}

section.offers-section {
  background: #f6f6f6;
  padding: 50px 15px;
}
section.offers-section .offers-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  max-width: 1100px;
  margin: auto;
}
@media (max-width: 767px) {
  section.offers-section .offers-wrapper {
    grid-template-columns: repeat(1, 1fr);
  }
}
section.offers-section .offers-wrapper .offer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
}
section.offers-section .offers-wrapper .offer .thinkus-item-wrapper {
  max-width: 459px;
  width: 100%;
}

section.offer-pagination {
  padding: 50px 15px 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 767px) {
  section.offer-pagination {
    padding: 40px 15px 40px;
  }
}
section.offer-pagination ul.offer-pagination-wrapper {
  padding: 0;
  list-style: none;
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  height: 70px;
}
@media (max-width: 767px) {
  section.offer-pagination ul.offer-pagination-wrapper {
    gap: 5px;
  }
}
section.offer-pagination ul.offer-pagination-wrapper li .page-numbers {
  font-size: 20px;
  font-weight: 700;
  padding: 17px 20px;
  border: 1px solid #00205b;
  border-radius: 10px;
  color: #00205b;
  transition: 0.3s;
}
@media (max-width: 767px) {
  section.offer-pagination ul.offer-pagination-wrapper li .page-numbers {
    padding: 11px 10px;
    font-size: 16px;
  }
}
section.offer-pagination ul.offer-pagination-wrapper li .page-numbers:hover, section.offer-pagination ul.offer-pagination-wrapper li .page-numbers.current {
  color: white;
  background-color: #00205b;
}
section.offer-pagination ul.offer-pagination-wrapper li .page-numbers.prev, section.offer-pagination ul.offer-pagination-wrapper li .page-numbers.next {
  padding: 17px 20px;
}
@media (max-width: 767px) {
  section.offer-pagination ul.offer-pagination-wrapper li .page-numbers.prev, section.offer-pagination ul.offer-pagination-wrapper li .page-numbers.next {
    padding: 12px 14px;
  }
}

img.header-image {
  width: 100%;
  min-height: 350px;
  object-fit: cover;
  max-height: 576px;
}
@media (max-width: 767px) {
  img.header-image {
    min-height: 80vh;
  }
}

section.offer-content {
  max-width: 1300px;
  margin: auto;
  background: #f6f6f6;
  position: relative;
  top: -8px;
  overflow: hidden;
  transition: 0.5s;
  padding: 7%;
}
section.offer-content .text-description {
  text-align: justify;
}
section.offer-content .offer-information {
  width: 0;
  float: left;
  overflow: hidden;
  /* &.active {
  	width: 100%;
  } */
}
section.offer-content .offer-submit {
  width: 0%;
  overflow: hidden;
  float: left;
}
section.offer-content .offer-submit.active {
  width: 100%;
}

section.similars-offers {
  max-width: 1300px;
  margin: auto;
  margin-top: 5%;
  margin-bottom: 5%;
  padding: 0 6%;
}

.text-description {
  margin-bottom: 25px;
  font-size: 22px;
}
@media (max-width: 767px) {
  .text-description {
    font-size: 18px;
  }
}

h2.offer-subtitle {
  font-size: 42px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  h2.offer-subtitle {
    font-size: 30px;
  }
}

button.activer-offert {
  background: #03dddd;
  font-size: 20px;
  color: #15255a;
  font-weight: 900;
  transition: 0.3s;
}
button.activer-offert.disabled {
  cursor: not-allowed;
  background: #03dddd !important;
  color: #15255a !important;
  opacity: 0.5;
}
@media (max-width: 991px) {
  button.activer-offert {
    font-size: 16px;
  }
}
button.activer-offert:hover {
  background: transparent;
  border-color: #03dddd;
  color: #03dddd;
}

header.header-vacante {
  position: relative;
}
header.header-vacante .header-wrpper {
  max-width: 1100px;
  margin: auto;
  padding: 0 15px;
  display: grid;
  grid-template-columns: 71% auto;
  gap: 20px;
  align-items: flex-end;
}
@media (max-width: 767px) {
  header.header-vacante .header-wrpper {
    grid-template-columns: 100%;
    gap: 20px;
  }
}
header.header-vacante .info-meta {
  display: flex;
  flex-wrap: wrap;
}
header.header-vacante h1.offer-title {
  font-size: 42px;
  color: #03dddd;
  margin: 0;
  font-weight: 900;
  font-family: "Raleway", sans-serif;
  margin-bottom: 25px;
}
@media (max-width: 767px) {
  header.header-vacante h1.offer-title {
    font-size: 38px;
  }
}
header.header-vacante .meta-components {
  display: flex;
  gap: 15px;
  padding-bottom: 15px;
  font-size: 18px;
  min-width: 24%;
  padding-right: 15px;
}
header.header-vacante .meta-components.Expired-data {
  min-width: 100%;
  padding-bottom: 0;
  padding-right: 0;
}
@media (max-width: 767px) {
  header.header-vacante .meta-components.Expired-data {
    font-size: 13px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  header.header-vacante .meta-components {
    padding-bottom: 15px;
    font-size: 15px;
    padding-right: 15px;
  }
}
@media (max-width: 767px) {
  header.header-vacante .meta-components {
    font-size: 16px;
    min-width: 45%;
  }
}
header.header-vacante .meta-components .meta-icon i {
  font-size: 1.4rem;
  color: white;
}
header.header-vacante .meta-components .meta-info {
  color: white;
  display: flex;
  flex-wrap: wrap;
}
header.header-vacante .meta-components .meta-info ul {
  padding-inline-start: 0px;
}
header.header-vacante .meta-components .meta-info ul li {
  list-style-type: none;
}
header.header-vacante .meta-components .meta-info ul li a {
  color: white;
}
header.header-vacante .header-content {
  position: absolute;
  z-index: 2;
  bottom: 36px;
  width: 100%;
  left: 0;
}

ul.requirement-list {
  padding: 0;
}

li.requirement-item {
  list-style-type: none;
}
li.requirement-item:before {
  content: "- ";
  display: inline-block;
  font-size: 2rem;
}

div#offer-list {
  overflow: hidden;
}

.thinkus-item-wrapper {
  background: white;
  border-radius: 10px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1607843137);
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: stretch;
  margin: 10px;
}
.thinkus-item-wrapper article.thinkus-item-container {
  min-width: 100%;
}
.thinkus-item-wrapper article.thinkus-item-container h3.thinkus-title {
  color: #05dddd;
  font-weight: 600;
  font-size: 26px;
}
.thinkus-item-wrapper article.thinkus-item-container .thinkus-excerpt {
  margin: 10px 0;
  font-size: 18px;
  color: #00205b;
}
.thinkus-item-wrapper article.thinkus-item-container .skills-list span.skill-item a,
.thinkus-item-wrapper article.thinkus-item-container .ubication-list span.skill-item a {
  font-size: 18px;
  font-weight: 600;
  color: #00205b;
  transition: 0.3s;
}
.thinkus-item-wrapper article.thinkus-item-container .skills-list span.skill-item:hover a,
.thinkus-item-wrapper article.thinkus-item-container .ubication-list span.skill-item:hover a {
  color: #03dddd;
}
.thinkus-item-wrapper article.thinkus-item-container .skills-list span.city-item a,
.thinkus-item-wrapper article.thinkus-item-container .ubication-list span.city-item a {
  font-size: 18px;
  font-weight: 600;
  color: #af14aa;
  transition: 0.3s;
}
.thinkus-item-wrapper article.thinkus-item-container .skills-list span.city-item:hover a,
.thinkus-item-wrapper article.thinkus-item-container .ubication-list span.city-item:hover a {
  color: #03dddd;
}

h2.postation-title {
  font-weight: bold;
  font-size: 38px;
  color: #15255a;
}

p.postulation-text {
  font-size: 22px;
  color: #15255a;
}
@media (max-width: 767px) {
  p.postulation-text {
    font-size: 18px;
  }
}

.postulation-header {
  margin-bottom: 50px;
}
@media (max-width: 767px) {
  .postulation-header {
    margin-bottom: 35px;
  }
}

.form-row {
  display: grid;
  width: 100%;
  justify-content: space-between;
  align-items: baseline;
  font-size: 22px;
  grid-template-columns: 35% 64%;
  gap: 1%;
  margin-bottom: 25px;
}
@media (max-width: 767px) {
  .form-row {
    grid-template-columns: 100%;
    font-size: 18px;
  }
}
.form-row label {
  display: flex;
  min-height: 42px;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 5px;
}
.form-row label .addtional-info {
  font-size: 16px;
  color: #737476;
  display: block;
  width: 100%;
  min-width: 100%;
}
.form-row label span.required {
  color: #c31cb5;
}
.form-row .postulation-input {
  width: 100%;
  display: block;
  border-color: #00205b;
  min-height: 42px;
  border-radius: 10px;
  background-color: transparent;
}
.form-row .postulation-input:focus-visible {
  border-color: #00205b;
}
.form-row p.info-alert {
  margin-bottom: 0;
  font-size: 0.7em;
  font-weight: 700;
  color: #c36;
  opacity: 0;
  display: none;
  transition: 0.7s;
}
.form-row p.info-alert.show {
  display: block;
  opacity: 1;
}

.aceptance-row {
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 22px;
}
.aceptance-row input#acceptance {
  font-size: 20px;
  width: 20px;
  height: 20px;
}
@media (max-width: 767px) {
  .aceptance-row {
    font-size: 18px;
  }
}
.aceptance-row a {
  color: #00b9ff;
}

button#send-offer {
  border-color: #c31cb5;
  color: white;
  background: #c31cb5;
  transition: 0.5s;
}
button#send-offer:hover {
  background: white;
  color: #c31cb5;
}
@media (min-width: 768px) {
  button#send-offer {
    max-width: 250px;
  }
}

.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview {
  width: 100% !important;
}
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-progress {
  width: 150px;
}
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-image {
  display: none;
}
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-details {
  font-size: 0.9rem;
  display: flex;
  width: 70%;
  position: absolute;
  top: -43px;
  overflow: hidden;
  line-height: 1;
}
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-error-mark svg {
  fill: red;
}
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-error-message span {
  font-size: 1rem;
}
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-success-mark svg {
  fill: green;
}
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-error-mark,
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-success-mark {
  position: absolute;
  left: 30px;
  top: -12px;
  width: 34px;
  height: 35px;
  left: 26%;
  margin-left: -27px;
}
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-error-mark svg,
.postulation-input-content div#mwp-dropform-wrapper .dz-preview.dz-file-preview .dz-success-mark svg {
  height: 34px;
  width: 34px;
}
.postulation-input-content div#mwp-dropform-wrapper div#dropzone-file .dz-default.dz-message {
  width: 100%;
  border: 1px solid;
  border-radius: 10px;
  display: flex;
  justify-content: flex-end;
  border-color: #00205b !important;
}
.postulation-input-content div#mwp-dropform-wrapper div#dropzone-file .dz-default.dz-message button.dz-button {
  border: none;
  padding: 5px 30px;
  font-size: 22px;
  color: #737476;
  font-weight: 400;
  background: transparent;
}
.postulation-input-content div#mwp-dropform-wrapper div#dropzone-file .dz-default.dz-message button.dz-button:focus, .postulation-input-content div#mwp-dropform-wrapper div#dropzone-file .dz-default.dz-message button.dz-button:focus-visible {
  border: none;
  outline: none;
}

.select2-container--default .select2-selection--multiple {
  background: transparent !important;
  border-color: #00205b !important;
}